@use '../base/variables'as v;
@use '../base/mixins'as m;

.boletos {
      padding: 5rem 0;

      h3 {
            text-align: center;
            color: v.$white;
            margin-bottom: 2REM
      }
}

.contenedor-boletos {
      @include m.tablet {
            @include m.grid(2, 1fr, 5rem);
      }
}

.pase {

     

      color: v.$black;

      .nombre-pase {
            text-align: center;
            color: v.$white;
            font-size: 2.6rem;
      }

      &-header,
      &-footer {
            padding: .5rem;
            margin: 0;

            p {
                  text-align: center;
                  color: v.$white;
                  text-transform: uppercase;
                  font-size: 1.6rem;
                  margin: .5rem;
            }
      }

      &-header {
            background-color: v.$rose;
      }

      &-body {
            background-color: v.$white;

            ul {
                  @include m.resetearlista;

                  li,
                  :nth-child(even) {
                        background-color: v.$white;
                        padding: 1rem;
                        border-bottom: 1px solid #e1e1e1;
                  }
            }
      }

      &-footer {
            background-color: v.$purple;

            p {
                  font-size: 2.4rem;
                  font-weight: 700;
            }

      }
}