@use '../base/variables' as v;
@use '../base/mixins' as m;

.header{
      background-color: v.$green;

      @include m.tablet {
            &.fijo{
                  top: 0;
                  left: 0;
                  position: fixed;
                  width: 100%;
                  transition: all .5s ease-in;
                  box-shadow: .1rem .2rem .6rem;
            }
      }

      h1{
            color: v.$white;
            text-align: center;
            ;

            @include m.tablet {
                  margin: 0;
            }
      }
      .contenido-header{
            padding: 2rem;
            @include m.tablet {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;
            }
      }
}

.navegacion-principal{
      
      @include m.tablet {
            display: flex;
            gap: 2rem;
      }
      
      a{
            display: block;
            text-align: center;
            margin-bottom: 1rem;
            color: v.$white;
            font-size: 2.4rem;
            
            @include m.tablet {
                  margin:0;
            }

            &:hover{
                  color: v.$yellow;
            }
      }
}



