@use 'variables' as v;
@use 'mixins' as m;
html {
      box-sizing: border-box;
      font-size: 62.5%;
}

*,
*:before,
*:after {
      box-sizing: inherit;
}

body {
      font-family: v.$main_font;
      font-size: 1.6rem;

      &.fijarBody{
            overflow: hidden;
      }
      &.body-scroll{
            padding-top:8.6rem;
      }
}

p{
      font-size: 2rem;
      color: v.$black;
      line-height: 1.5;
}

.contenedor{
      width: 95%;
      max-width: 120rem;
      margin: 0 auto;
}

a{
      text-decoration: none;
      color: v.$black;
}
h1,h2,h3{
      margin: 0 0 5rem 0;
}
h1{
      font-size: 4rem;
}
h2{
      font-size: 3.6rem;
}
h3{
      font-size: 3rem;
}
video{
      display: block;
      width: auto; 
      @include m.tablet {
            width: 100%;    
      }
      background-size: cover;
      background-position: center center;
}

img{
      width: 100%;
      height: auto;
      max-width: 100%;
      display: block;
}
picture{
      max-width: 100%;
      display: block;
}