@use 'variables' as v;


@mixin tel{
      @media screen and (max-width:v.$tablet) {
            @content;
      }
}
@mixin tablet{
      @media screen and (min-width:v.$tablet){
            @content;
      }
}

@mixin desktop{
      @media screen and (min-width:v.$desktop){
            @content;
      }
}
@mixin desktopXL{
      @media screen and (min-width:v.$desktopXL){
            @content;
      }
}

@mixin grid($column, $dimention, $space){
      display: grid;
      grid-template-columns: repeat($column,$dimention);
      gap: $space;
}

@mixin resetearlista{
      text-decoration: none;
      list-style: none;
      margin: 0;
      padding: 0;
}