@use '../base/variables'as v;
@use '../base/mixins'as m;

.video {
      position: relative;
      max-height: 70rem;
      overflow: hidden;

      video {
            width: 100%;
      }

      .overlay {
            position: absolute;
            height: 100%;
            width: 100%;
            background: rgb(165, 3, 255);
            background: linear-gradient(270deg, rgba(165, 3, 255, 0.5) 0%, rgba(255, 0, 110, 0.3) 100%);
            display: flex;
            flex-direction: column;
            justify-content: center;

      }
}

.contenido-video {
      z-index: 1;

      h2 {
            color: v.$white;
            font-size: 7rem;
            margin: 0;

            @include m.tel {
                  font-size: 3rem;
            }
      }

      p {
            color: v.$white;
            background-color: v.$rose;
            display: inline-block;
            padding: 1rem 2rem;
            font-weight: 700;
            text-shadow: 2px 2px 0 #020202, 4px 4px 0 #000000;
            margin: 0;

            @include m.tel {
                  font-size: 1rem;
                  padding: .3rem .8rem;
                  font-weight: 700;
                  text-shadow: 2px 2px 0 #020202, 4px 4px 0 #000000;

            }
      }

      span {
            color: v.$black;
            font-size: 1.2rem;
            text-shadow: none;

            @include m.tel {
                  font-size: .9rem;
                  text-shadow: none;
            }
      }
}