@use '../base/variables'as v;
@use '../base/mixins'as m;

.lineup {
      background-color: v.$rose;
      padding: 5rem 0;

      h3,
      p {
            text-align: center;
            color: v.$white;
      }

      .dia {
            text-transform: uppercase;
            margin: 5rem 0;
      }
}

.escenarios-contenedor {
      @include m.tablet {
            @include m.grid(2, 1fr, 5rem);
      }

      p {
            color: v.$black;
      }

}

.escenario {
      padding-top: 2rem;

      -webkit-box-shadow: 10px 8px 12px -6px rgba(0, 0, 0, 0.59);
      -moz-box-shadow: 10px 8px 12px -6px rgba(0, 0, 0, 0.59);
      box-shadow: 10px 8px 12px -6px rgba(0, 0, 0, 0.59);

      p {
            &::after {
                  content: '';
                  display: block;
                  width: 20rem;
                  height: 10rem;
                  background-size: 15rem;
                  background-repeat: no-repeat;
                  background-position: center center;
                  margin: 2rem auto 0 auto;
            }
      }
}

.EDM .nombre-escenario:after {
      background-image: url(../../../build/img/icono_edm.png);
}

.rock .nombre-escenario:after {
      background-image: url(../../../build/img/icono_rock.png);
}

.calendario {
      @include m.resetearlista;

      li {
            padding: 2.5rem;
            font-weight: 700;

            -webkit-box-shadow: 0px 8px 12px -6px rgba(0, 0, 0, 0.59);
            -moz-box-shadow: 0px 8px 12px -6px rgba(0, 0, 0, 0.59);
            box-shadow: 0px 8px 12px -6px rgba(0, 0, 0, 0.59);

            span {
                  font-weight: 400;
            }
      }

     
}

.bg-amarillo {
      background-color: v.$yellow;

      li:nth-child(odd) {
            background-color: darken(v.$yellow, 10%);
      }
}

.bg-verde {
      background-color: v.$green;

      li:nth-child(even) {
            background-color: darken(v.$green, 10%);
      }
}