@use '../base/variables'as v;
@use '../base/mixins'as m;

.galeria {
      padding: 5rem 0 ;

      h3 {
            text-align: center;
      }

      
}

.galeriaimg {
      @include m.resetearlista;

      @include m.tel {

            @include m.grid(2, 1fr, 2rem);
      }

      @include m.tablet {

            @include m.grid(3, 1fr, 2rem);
      }

      @include m.desktop {

            @include m.grid(4, 1fr, 2rem);
      }

      @include m.desktopXL {

            @include m.grid(6, 1fr, 2rem);
      }
}

.overlay {
      position: fixed;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;

      width: 100%;
      height: 100%;

      display: grid;
      place-items: center;

      background-color: rgb(0 0 0 / .5);

      picture {
            display: block;
            max-width: 80rem;
      }
}

.btnCerrar {
      width: 3rem;
      height: 3rem;
      border-radius: 50%;
      background-color: v.$white;
      font-weight: 700;

      display: grid;
      place-items: center;

      &:hover {
            cursor: pointer;
      }
}